import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';
import { HttpService } from 'src/app/services/http.service';
import { environment } from 'src/environments/environment';
import { getBannerParams } from 'src/app/services/http-helpers';
import { ApiHelperService } from 'src/app/services/api-helper.service';
import { Banner, BannerResp } from './banner.model';

@Component({
  selector: 'app-banner-section',
  templateUrl: './banner-section.component.html',
  styleUrls: ['./banner-section.component.scss'],
})
export class BannerSectionComponent implements OnInit, OnDestroy {
  banners!: Banner[];
  private fetchBannerSubs!: Subscription;

  constructor(
    private apiHelperService: ApiHelperService,
    private translateService: TranslateService,
    private httpService: HttpService
  ) {}

  ngOnInit() {
    this.getBannerImages();

    this.httpService
      .sendGetRequest<any>({
        url: 'https://reqres.in/api/users?page=2',
      })
      .subscribe({
        next: (users) => {
          console.log(users);
        },
      });
  }

  private getBannerImages() {
    this.fetchBannerSubs = this.httpService
      .sendGetRequest<BannerResp>({
        url: `${environment.CORE_API_URL}/fetchbanners`,
        params: getBannerParams(this.translateService.currentLang),
      })
      .pipe(
        filter(this.apiHelperService.getApiSuccessFilterFn<BannerResp>()),
        map(({ pd: { listHeroSpace } }) => listHeroSpace)
      )
      .subscribe({
        next: (banners) => {
          this.banners = banners;
        },
      });
  }

  ngOnDestroy() {
    this.fetchBannerSubs.unsubscribe();
  }
}
