import { Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { isPlatformServer } from '@angular/common';
import {
  HttpEvent,
  HttpEventType,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
} from '@angular/common/http';
import { Observable, tap } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class HttpAssetsInterceptor implements HttpInterceptor {
  constructor(@Inject(PLATFORM_ID) private platformId: string) {}

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    if (!isPlatformServer(this.platformId)) {
      return next.handle(req);
    }

    const assetsBaseUrl = `${environment.isLocal ? 'http' : 'https'}://${
      environment.isLocal ? 'localhost:4200' : environment.deployUrl
    }${environment.baseUrl}`;

    const reqWithOrigin = req.clone({
      url: req.url.replace(
        '.',
        req.url.includes('assets') ? assetsBaseUrl : '.'
      ),
      headers: req.headers.set('x-requested-with', environment.deployUrl),
    });

    return next.handle(reqWithOrigin).pipe(
      tap({
        next: (resp) => {
          if (resp.type === HttpEventType.Response) {
            console.log('Client Success::', reqWithOrigin.url);
            console.log(resp.body);
          }
        },
        error: (error) => {
          console.log('Client::', reqWithOrigin.url);
          console.log(error);
        },
      })
    );
  }
}
