import { Component, OnDestroy, OnInit } from '@angular/core';
import { HttpParams } from '@angular/common/http';
import { filter, forkJoin, map, Subscription } from 'rxjs';
import { HttpService } from 'src/app/services/http.service';
import { environment } from 'src/environments/environment';
import { getAuthHeaders } from 'src/app/services/http-helpers';
import { AllStatistics, ResultJSON, ServiceStats } from './statistics.model';

@Component({
  selector: 'app-statistics-section',
  templateUrl: './statistics-section.component.html',
  styleUrls: ['./statistics-section.component.scss'],
})
export class StatisticsSectionComponent implements OnInit, OnDestroy {
  allStatistics!: AllStatistics;
  private allStatsSubs!: Subscription;

  constructor(private httpService: HttpService) {}

  ngOnInit() {
    this.getAllStatistics();
  }

  private getAllStatistics() {
    this.allStatsSubs = forkJoin([
      this.getResultJson(),
      this.getServiceStats(),
    ]).subscribe({
      next: ([resultStats, serviceStats]) => {
        this.allStatistics = { ...resultStats, ...serviceStats };
        this.allStatistics.serviceStats;
      },
    });
  }

  private getResultJson() {
    const resultJsonParams = new HttpParams().append(
      'api_key',
      environment.resultsJSONApiKey
    );

    return this.httpService
      .sendGetRequest<ResultJSON>({
        params: resultJsonParams,
        headers: getAuthHeaders(),
        url: `${environment.API_BASE_URL}/api/queries/45/results.json`,
      })
      .pipe(
        map(({ query_result }) => {
          const {
            data: { rows },
          } = query_result;

          return {
            transactions: rows[0].Transactions,
            registeredUsers: rows[0].total_registered_users,
          };
        })
      );
  }

  private getServiceStats() {
    return this.httpService
      .sendGetRequest<ServiceStats>({
        url: `${environment.API_BASE_URL}/getServiceStats`,
        headers: getAuthHeaders(),
      })
      .pipe(
        filter(({ rs }) => rs.toLowerCase() === 's'),
        map(({ pd }) => {
          const {
            services_stats: { departments, services },
          } = pd;
          const serviceStats = {
            billPayments: +services.utility_bill,
            otherServices:
              (+services.central || 0) +
              (+services.state || 0) +
              (+services.serviceplus || 0),
          };
          const deptStats = {
            state: +departments.state,
            central: +departments.central,
          };

          return { serviceStats, deptStats };
        })
      );
  }

  ngOnDestroy() {
    this.allStatsSubs.unsubscribe();
  }
}
