import { Component, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { HttpService } from 'src/app/services/http.service';
import { environment } from 'src/environments/environment';
import { getBannerParams } from 'src/app/services/http-helpers';
import { PopularService } from './popular-services.model';

@Component({
  selector: 'app-popular-services-section',
  templateUrl: './popular-services-section.component.html',
  styleUrls: ['./popular-services-section.component.scss'],
})
export class PopularServicesSectionComponent implements OnInit, OnDestroy {
  popularServices!: PopularService[];
  private popularServiceSubs!: Subscription;

  constructor(
    private translateService: TranslateService,
    private httpService: HttpService
  ) {}

  ngOnInit() {
    this.getPopularServices();
  }

  private getPopularServices() {
    this.popularServiceSubs = this.httpService
      .sendGetRequest<PopularService[]>({
        url: `${environment.CORE_API_URL}/popularservices`,
        params: getBannerParams(this.translateService.currentLang, 'app'),
      })
      .subscribe({
        next: (popularServices) => {
          this.popularServices = popularServices;
        },
      });
  }

  ngOnDestroy() {
    this.popularServiceSubs.unsubscribe();
  }
}
