import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { CategoriesService } from 'src/app/services/categories.service';
import { Category } from '../../../services/categories-model';

@Component({
  selector: 'app-categories-section',
  templateUrl: './categories-section.component.html',
  styleUrls: ['./categories-section.component.scss'],
})
export class CategoriesSectionComponent implements OnInit, OnDestroy {
  categories!: Category[];
  private getCategoriesSubs!: Subscription;

  constructor(private categoriesService: CategoriesService) {}

  ngOnInit() {
    this.getCategories();
  }

  private getCategories() {
    this.getCategoriesSubs = this.categoriesService
      .getAllCategories()
      .subscribe({
        next: (categories) => {
          this.categories = categories;
        },
      });
  }

  ngOnDestroy() {
    this.getCategoriesSubs.unsubscribe();
  }
}
