import { HttpHeaders, HttpParams } from '@angular/common/http';
import { environment } from 'src/environments/environment';

export function getAuthHeaders() {
  return new HttpHeaders({ 'x-api-key': environment.xApiKey });
}

export function getBannerParams(lang: string, mode = 'web') {
  return new HttpParams()
    .append('lang', lang)
    .append('os', 'web')
    .append('mod', mode)
    .append('ver', '');
}

export function getAllServicesParams(lang: string) {
  return new HttpParams()
    .append('lang', lang)
    .append('os', 'web')
    .append('mod', 'app')
    .append('ldate', '')
    .append('depttype', 'all')
    .append('st', '');
}

export function getHomeServicesParams(lang: string, serviceCardNo = '') {
  return getAllServicesParams(lang)
    .set('depttype', 'undefined')
    .append('ver', '')
    .append('servicecardno', serviceCardNo);
}
