import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ErrorDialogComponent } from '../components/utility/error-dialog/error-dialog.component';

@Injectable({
  providedIn: 'root',
})
export class ApiHelperService {
  constructor(private matDialog: MatDialog) {}

  /**
   * Method used to check for response status (rs) on API response body.
   * @returns boolean value indicating the response is correct or not.
   */
  getApiSuccessFilterFn<T>() {
    return (resp: T) => {
      const isAPIRespSuccess = (<any>resp).rs.toLowerCase() === 'su';

      if (!isAPIRespSuccess) {
        this.matDialog.open(ErrorDialogComponent);
      }

      return isAPIRespSuccess;
    };
  }
}
