import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

import { HomeComponent } from './pages/home/home.component';
import { AboutUsComponent } from './pages/about-us/about-us.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { ServicesComponent } from './pages/services/services.component';
import { OurPartnersComponent } from './pages/our-partners/our-partners.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { ServicesRouteResolver } from './resolvers/services-route.resolver';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent,
  },
  {
    path: 'about-us',
    component: AboutUsComponent,
  },
  {
    path: 'services',
    resolve: { servicesPlusCtgs: ServicesRouteResolver },
    component: ServicesComponent,
  },
  { path: 'dashboard', component: DashboardComponent },
  { path: 'partners', component: OurPartnersComponent },
  { path: '**', component: PageNotFoundComponent },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      initialNavigation: 'enabledBlocking',
      preloadingStrategy: PreloadAllModules,
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
