import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { forkJoin, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';
import { CachingService } from '../services/caching.service';
import { HttpService } from '../services/http.service';
import { CategoriesService } from '../services/categories.service';
import { ApiHelperService } from '../services/api-helper.service';
import { environment } from 'src/environments/environment';
import { getAllServicesParams, getAuthHeaders } from '../services/http-helpers';
import { cacheKeys } from '../constants/cache-keys';
import { AllServicesResp, Service } from '../pages/services/all-services.modal';
import { Category } from '../services/categories-model';

interface ServiceRouteData {
  categories: Category[];
  allServices: Service[];
}

@Injectable({
  providedIn: 'root',
})
export class ServicesRouteResolver implements Resolve<ServiceRouteData> {
  constructor(
    private apiHelperService: ApiHelperService,
    private translateService: TranslateService,
    private categoriesService: CategoriesService,
    private cachingService: CachingService,
    private httpService: HttpService
  ) {}

  resolve() {
    const getCachedCategories = () =>
      this.cachingService.fetchCachedData<Category[]>(cacheKeys.categories);

    const getCachedServices = () =>
      this.cachingService.fetchCachedData<Service[]>(cacheKeys.allServices);

    const fetchAllServicesFromAPI = () => {
      return this.httpService
        .sendGetRequest<AllServicesResp>({
          url: `${environment.CORE_API_URL}/allservices`,
          headers: getAuthHeaders(),
          params: getAllServicesParams(this.translateService.currentLang),
        })
        .pipe(
          map((resp) => {
            const {
              pd: { addServiceList },
            } = resp;
            const errorCheckFn =
              this.apiHelperService.getApiSuccessFilterFn<AllServicesResp>();

            if (!errorCheckFn(resp)) {
              throw new Error('Error in fetching all services');
            }
            this.cachingService.storeCachedData(
              cacheKeys.allServices,
              addServiceList
            );
            return addServiceList;
          })
        );
    };

    const cachedAllServices = getCachedServices();
    const categoriesObs$ = this.categoriesService.getAllCategories();
    const allServicesObs$ = cachedAllServices?.length
      ? of(cachedAllServices)
      : fetchAllServicesFromAPI();

    return forkJoin({
      categories: categoriesObs$,
      allServices: allServicesObs$,
    }).pipe(
      catchError((error) => {
        // If any API throws an error then the cached data (if any) would be returned
        return of({
          categories: getCachedCategories(),
          allServices: getCachedServices(),
        });
      })
    );
  }
}
