import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import {
  HttpClient,
  HttpClientModule,
  HTTP_INTERCEPTORS,
} from '@angular/common/http';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {
  TranslateLoader,
  TranslateModule,
  TranslateService,
} from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { NgxJsonLdModule } from 'ngx-json-ld';
import { AppRoutingModule } from './app-routing.module';
import { AngularMaterialModule } from './modules/angular-material.module';

import { HttpAssetsInterceptor } from './interceptors/http-assets.interceptor';

import { AppComponent } from './app.component';
import { HomeComponent } from './pages/home/home.component';
import { AboutUsComponent } from './pages/about-us/about-us.component';
import { ServicesComponent } from './pages/services/services.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { OurPartnersComponent } from './pages/our-partners/our-partners.component';
import { NavbarComponent } from './components/layout/navbar/navbar.component';
import { LanguageSelectionComponent } from './components/utility/language-selection/language-selection.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { FooterComponent } from './components/layout/footer/footer.component';
import { StatisticsSectionComponent } from './components/sections/statistics-section/statistics-section.component';
import { StatisticsCardComponent } from './components/utility/statistics-card/statistics-card.component';
import { BannerSectionComponent } from './components/sections/banner-section/banner-section.component';
import { ErrorDialogComponent } from './components/utility/error-dialog/error-dialog.component';
import { WhatsNewSectionComponent } from './components/sections/whats-new-section/whats-new-section.component';
import { PopularServicesSectionComponent } from './components/sections/popular-services-section/popular-services-section.component';
import { TrendingServicesSectionComponent } from './components/sections/trending-services-section/trending-services-section.component';
import { CategoriesSectionComponent } from './components/sections/categories-section/categories-section.component';
import { APP_BASE_HREF } from '@angular/common';
import { environment } from 'src/environments/environment';



export function HttpLoaderFactory(httpClient: HttpClient) {
  return new TranslateHttpLoader(httpClient, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    AppComponent,
    AboutUsComponent,
    ServicesComponent,
    DashboardComponent,
    OurPartnersComponent,
    HomeComponent,
    NavbarComponent,
    LanguageSelectionComponent,
    PageNotFoundComponent,
    FooterComponent,
    StatisticsSectionComponent,
    StatisticsCardComponent,
    BannerSectionComponent,
    ErrorDialogComponent,
    WhatsNewSectionComponent,
    PopularServicesSectionComponent,
    TrendingServicesSectionComponent,
    CategoriesSectionComponent,
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'serverApp' }),
    AppRoutingModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    NgxJsonLdModule,
    AngularMaterialModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient],
      },
    }),
  ],
  providers: [
    { provide: APP_BASE_HREF, useValue: environment.baseUrl },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: HttpAssetsInterceptor,
      multi: true,
    },
  ],
  bootstrap: [AppComponent],
})
export class AppModule {
  constructor(private translateService: TranslateService) {
    this.setCurrentTranslation();
  }

  private setCurrentTranslation() {
    // TODO: Logic for reading translation from langDate.
    this.translateService.use('en');
  }
}
