import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { states } from 'src/app/constants/states';
import { Service, ServiceFilter } from './all-services.modal';
import { Category } from 'src/app/services/categories-model';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.scss'],
})
export class ServicesComponent implements OnInit {
  categories!: Category[];
  allServices!: Service[];
  filteredServices!: Service[];
  filterConfig!: ServiceFilter;
  states = states;

  constructor(private activatedRoute: ActivatedRoute) {}

  ngOnInit() {
    this.getServicesPlusCtgs();
    this.initServiceFilter();
  }

  private getServicesPlusCtgs() {
    const {
      servicesPlusCtgs: { categories, allServices },
    } = this.activatedRoute.snapshot.data;

    this.categories = categories;
    this.allServices = allServices;
  }

  private initServiceFilter() {
    this.filterConfig = {
      serviceName: '',
      state: this.activatedRoute.snapshot.queryParams['stateId'] || '0',
      category: this.activatedRoute.snapshot.queryParams['category'],
    };

    this.onFilterServices();
  }

  onEditCategory(categoryId: string) {
    const isCtgSelected = this.filterConfig.category === categoryId;

    this.filterConfig.category = isCtgSelected ? '' : categoryId;
    this.onFilterServices();
  }

  onFilterServices() {
    if (!this.allServices?.length) {
      return;
    }

    const checkForOtherState = (stateId: string, otherState: string) => {
      if (!otherState) {
        return false;
      }

      return otherState.split('|').includes(stateId);
    };

    const {
      serviceName: filterName,
      state: filterState,
      category: filterCategory,
    } = this.filterConfig;

    this.filteredServices = this.allServices.filter(
      ({ serviceName, state, otherState, categoryId }) => {
        const isNameMatch = serviceName
          ?.toLowerCase()
          .includes(filterName?.toLowerCase());

        // filterState is converted to a number bcz we don't want filtering on "0" (All) states
        const isStateMatch = +filterState
          ? state === filterState || checkForOtherState(filterState, otherState)
          : true;
        const isCategoryMatch = !!filterCategory
          ? categoryId === filterCategory
          : true;

        return isNameMatch && isStateMatch && isCategoryMatch;
      }
    );
  }
}
