import { Component, OnDestroy, OnInit } from '@angular/core';
import { filter, map, Subscription } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { HttpService } from 'src/app/services/http.service';
import { ApiHelperService } from 'src/app/services/api-helper.service';
import { environment } from 'src/environments/environment';
import { getHomeServicesParams } from 'src/app/services/http-helpers';
import {
  WhatsNewService,
  WhatsNewServiceResp,
} from './whats-new-services.model';

@Component({
  selector: 'app-whats-new-section',
  templateUrl: './whats-new-section.component.html',
  styleUrls: ['./whats-new-section.component.scss'],
})
export class WhatsNewSectionComponent implements OnInit, OnDestroy {
  whatsNewServices!: WhatsNewService[];
  private whatsNewServiceSubs!: Subscription;

  constructor(
    private apiHelperService: ApiHelperService,
    private translateService: TranslateService,
    private httpService: HttpService
  ) {}

  ngOnInit() {
    this.getWhatsNewServices();
  }

  private getWhatsNewServices() {
    this.whatsNewServiceSubs = this.httpService
      .sendGetRequest<WhatsNewServiceResp>({
        url: `${environment.CORE_API_URL}/newandupdatedservicecard`,
        params: getHomeServicesParams(this.translateService.currentLang, '2'),
      })
      .pipe(
        filter(
          this.apiHelperService.getApiSuccessFilterFn<WhatsNewServiceResp>()
        ),
        map(({ pd: { serviceCardList } }) => serviceCardList)
      )
      .subscribe({
        next: (whatsNewServices) => {
          this.whatsNewServices = whatsNewServices;
        },
      });
  }

  ngOnDestroy() {
    this.whatsNewServiceSubs.unsubscribe();
  }
}
