import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';
import { of } from 'rxjs';
import { map } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';
import { CachingService } from './caching.service';
import { HttpService } from './http.service';
import { ApiHelperService } from './api-helper.service';
import { environment } from 'src/environments/environment';
import { cacheKeys } from '../constants/cache-keys';
import { CategoriesResp, Category } from './categories-model';

@Injectable({
  providedIn: 'root',
})
export class CategoriesService {
  constructor(
    private translateService: TranslateService,
    private cachingService: CachingService,
    private httpService: HttpService,
    private apiHelperService: ApiHelperService
  ) {}

  /**
   * Method to get the service categories.
   * @returns Observable that resolves into categories.
   */
  getAllCategories() {
    const fetchCategoriesFromAPI = () => {
      const fetchCategoryParams = new HttpParams().append(
        'lang',
        this.translateService.currentLang
      );

      return this.httpService
        .sendGetRequest<CategoriesResp>({
          url: `${environment.CORE_API_URL}/fetchcategory`,
          params: fetchCategoryParams,
        })
        .pipe(
          map((resp) => {
            const {
              pd: { primaryCategoryList },
            } = resp;
            const errorCheckFn =
              this.apiHelperService.getApiSuccessFilterFn<CategoriesResp>();

            if (!errorCheckFn(resp)) {
              throw new Error('Error in fetching categories');
            }
            this.cachingService.storeCachedData<Category[]>(
              cacheKeys.categories,
              primaryCategoryList
            );
            return primaryCategoryList;
          })
        );
    };

    const cachedCategories = this.cachingService.fetchCachedData<Category[]>(
      cacheKeys.categories
    );

    return cachedCategories?.length
      ? of(cachedCategories)
      : fetchCategoriesFromAPI();
  }
}
