import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';
import { HttpService } from 'src/app/services/http.service';
import { ApiHelperService } from 'src/app/services/api-helper.service';
import { environment } from 'src/environments/environment';
import { getHomeServicesParams } from 'src/app/services/http-helpers';
import {
  TrendingService,
  TrendingServiceResp,
} from './trending-services.model';

@Component({
  selector: 'app-trending-services-section',
  templateUrl: './trending-services-section.component.html',
  styleUrls: ['./trending-services-section.component.scss'],
})
export class TrendingServicesSectionComponent implements OnInit, OnDestroy {
  trendingServices!: TrendingService[];
  private trendingServiceSubs!: Subscription;

  constructor(
    private apiHelperService: ApiHelperService,
    private translateService: TranslateService,
    private httpService: HttpService
  ) {}

  ngOnInit() {
    this.getTrendingServices();
  }

  private getTrendingServices() {
    this.trendingServiceSubs = this.httpService
      .sendGetRequest<TrendingServiceResp>({
        url: `${environment.CORE_API_URL}/trendingservicecard`,
        params: getHomeServicesParams(this.translateService.currentLang, '3'),
      })
      .pipe(
        filter(
          this.apiHelperService.getApiSuccessFilterFn<TrendingServiceResp>()
        ),
        map(({ pd: { serviceCardList } }) => serviceCardList)
      )
      .subscribe({
        next: (trendingServices) => {
          this.trendingServices = trendingServices;
        },
      });
  }

  ngOnDestroy() {
    this.trendingServiceSubs.unsubscribe();
  }
}
